import blogApi from "@/api/blog";

export default {
  data() {
    return {
      page: 1, //当前页
      data: {}, //课程列表
      subjectNestedList: [], // 一级分类列表
      subSubjectList: [], // 二级分类列表
      searchObj: {}, // 查询表单对象
      oneIndex: -1,
      twoIndex: -1,
      buyCountSort: "",
      gmtCreateSort: "",
      priceSort: ""
    };
  },
  created() {
   
  },
  mounted(){
 //博客第一次查询
    this.initBlogFirst();
    //一级分类额显示
    this.initSubject();
  },
  methods: {
    //1.查询第一页数据
    initBlogFirst() {
      blogApi.getBlogList(1, 8, this.searchObj).then(response => {
        this.data = response.data.data;
        console.log(this.data);
      });
    },
    //2查询所有的分类
    initSubject() {
      blogApi.getAllSubject().then(response => {
        this.subjectNestedList = response.data.data.list;
      });
    },
    //分页切换的方法
    gotoPage(page) {
      blogApi.getBlogList(page, 8, this.searchObj).then(response => {
        this.data = response.data.data;
      });
    },
    //点击某个一级分类，查询对应的二级分类
    searchOne(subjectParentId, index) {
      //把传递index值赋值给oneIndex为了active样式生效
      this.oneIndex = index;

      this.twoIndex = -1;
      this.searchObj.subjectId = "";
      this.subSubjectList = [];

      //把一级分类点击id值，赋值给searchObj
      this.searchObj.subjectParentId = subjectParentId;
      //点击某个一级分类进行条件查询
      this.gotoPage(1);

      //拿着点击一级分类Id和所有一级分类id进行比较
      //如果id相同，从一级分类里面获取对应的二级分类
      for (let i = 0; i < this.subjectNestedList.length; i++) {
        //获取每个一级分类
        var oneSubject = this.subjectNestedList[i];
        console.log(oneSubject);
        //比较id是否相同
        if (subjectParentId == oneSubject.id) {
          //从一级分类里面获取对应的二级分类
          this.subSubjectList = oneSubject.children;
        }
      }
    },
    //点击某个二级分类实现查询
    searchTwo(subjectId, index) {
      //把index赋值，为了样式生效
      this.twoIndex = index;
      //把二级分类点击id值，赋值给seachObj
      this.searchObj.subjectId = subjectId;
      //点击某个二级分类进行调价查询
      this.gotoPage(1);
    }
  }
};
